@use "sass:math";

.btn {
	background: #900;
	border-radius: 5px;
	color: #fff;
	display: inline-block;
	font-weight: 600;
	padding: 5px 10px;
	text-shadow: 0 -1px 0 rgba(0, 0, 16, 0.5);
}

.btn:hover,
.btn:focus {
	background: #c00;
	box-shadow: inset 0 0 20px #900;
	color: #fff;
	text-decoration: none;
}

.btn--nav:before {
	content: "";
	padding-right: math.div($spacer, 4);
}
